@import "../../base.scss";

.card-movieTV {
  border-radius: 0.75rem;
  width: 100%;
  height: 100%;
  margin-right: 0.5rem;

  &:last-child {
    margin-right: 0;
  }

  .poster {
    // object-fit: contain;
    width: 200px;
    max-height: 500px;
    border-radius: 0.75rem;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    box-shadow: $shadow-card;
    @include respon(phone-pot) {
      width: 170px;
      max-height: 450px;
    }

    &:hover {
      transform: scale(1.2);
      margin: 0 2rem;
    }
  }
}
